import React from 'react';
import { Button, Form } from 'react-bootstrap';

const UploadForm = () => {
  return (
    <div className="upload-container d-flex justify-content-center align-items-center">
      <div>
        <h1 className="text-center mb-4">AiRYA Upload Form</h1>
        <Form>
          <Form.Group controlId="songTitle">
            <Form.Label>Song Title</Form.Label>
            <Form.Control type="text" placeholder="Enter song title" />
          </Form.Group>

          <Form.Group controlId="releaseDate">
            <Form.Label>Release Date</Form.Label>
            <Form.Control type="date" />
          </Form.Group>

          <Form.Group controlId="songCredits">
            <Form.Label>Song Credits</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Paste credits here or upload PDF below" />
            <Form.File id="songCreditsFile" label="Upload Credit PDF" custom className="mt-2" />
          </Form.Group>

          <Form.Group controlId="songAudio">
            <Form.Label>Audio</Form.Label>
            <Form.File id="songAudioFile" label="Upload Audio File" custom />
          </Form.Group>

          <Form.Group controlId="instructions">
            <Form.Label>Specific Instructions</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Enter any special release requests or other instructions" />
          </Form.Group>

          <Button variant="primary" type="submit">
            Submit
          </Button>
        </Form>
      </div>
    </div>
  );
}

export default UploadForm;
